:root {
  --bg-void: #050505;
  --glass-surface: rgba(20, 20, 20, 0.7);
  --neon-red: #FF4D4D;
  --neon-gold: #FFD700;
  --text-muted: #666666;
}

body {
  background-color: var(--bg-void);
  color: white;
  overflow: hidden;
  /* Prevent scroll */
}

/* 1. The Beacon Pulse Animation */
@keyframes beacon-pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(255, 77, 77, 0.4);
    transform: scale(1);
  }

  50% {
    box-shadow: 0 0 0 20px rgba(255, 77, 77, 0);
    transform: scale(1);
  }

  100% {
    box-shadow: 0 0 0 0 rgba(255, 77, 77, 0);
    transform: scale(1);
  }
}

/* 2. The "Spotlight" Marker Styles */
.marker-danger {
  background-color: var(--neon-red);
  border: 2px solid white;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  box-shadow: 0 0 20px var(--neon-red);
  animation: beacon-pulse 1.5s infinite;
  z-index: 50;
  /* Always on top */
  transition: all 0.3s ease;
}

.marker-safe {
  background-color: #3B82F6;
  /* Dim Blue */
  width: 8px;
  height: 8px;
  border-radius: 50%;
  opacity: 0.5;
  /* Fade into background */
  z-index: 10;
  transition: all 0.3s ease;
}

/* 3. Glass Panels */
.neo-glass {
  background: var(--glass-surface);
  backdrop-filter: blur(24px);
  border: 1px solid rgba(255, 255, 255, 0.05);
}
